<template>
	<section>
		<h3>现场图片</h3>
		<el-upload class="avatar-uploader" action="//up.qbox.me/" :show-file-list="false" :data="uploadform" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
			<img v-if="img" :src="img" class="avatar">
			<i v-else class="el-icon-plus avatar-uploader-icon"></i>
		</el-upload>
		<h3>文字</h3>
		<el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" placeholder="请输入内容" v-model="textarea" style="width:600px;"></el-input>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				img:'',
				textarea:'',
			};
		},
		methods:{
			handleAvatarSuccess(response, file, fileList) {
      this.ruleForm.img = `${this.host.imgBaseUrl}${response.key}`;
			},
			beforeAvatarUpload(file) {
				let isLt2M = file.size / 1024 / 1024 < 1;
				let _this = this;

				if (!isLt2M) {
					_this.$message.error("上传图片大小不能超过 1MB!");
				} else {
					let params = {
						fileName: file.name
					};

					return axios
						.get(_this.host.baseUrl + "/uploadimg", { params })
						.then(res => {
							if (res.data.code === "1") {
								_this.uploadform = {
									key: res.data.result.fileName,
									token: res.data.result.uploadToken
								};
							} else {
								_this.$message.error("上传图片失败");
							}
						})
						.catch(() => {
							_this.$message.error("上传图片失败");
						});
				}

				return isLt2M;
			},
		}
	}

</script>

<style>
.avatar-uploader-icon {
	font-size: 28px;
	color: #8c939d;
	width: 150px;
	height: 150px;
	line-height: 150px;
	text-align: center;
	border: 1px dashed #8c939d;
}
.avatar {
	width: 150px;
	height: 150px;
	display: block;
}
</style>


